<template>
    <div>
        <div class="row">
            <div class="col-sm-12">
                <div class="card">
                    <div class="card-header">
                        <h5 class="card-title">Datos Generales</h5>
                    </div>
                    <div v-if="trade && trade.tra_id">
                        <div class="card-body">
                            <div class="row py-2">
                                <div class="col-md-6">
                                    <div class="row">
                                        <div class="col-xl-5 col-sm-4 fw-bold">
                                            Expediente N°
                                        </div>
                                        <div class="col-xl-7 col-sm-8 fw-bold">
                                            : {{ trade.tra_number }}
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="row py-2">
                                <div class="col-md-6">
                                    <div class="row">
                                        <div class="col-xl-5 col-sm-4">
                                            Nombre
                                        </div>
                                        <div class="col-xl-7 col-sm-8">
                                            : {{ trade.tra_name }}
                                        </div>
                                    </div>
                                </div>
                                <div class="col-md-6">
                                    <div class="row">
                                        <div class="col-xl-5 col-sm-4">
                                            Asunto
                                        </div>
                                        <div class="col-xl-7 col-sm-8">
                                            : {{ trade.tra_matter }}
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="row py-2">
                                <div class="col-md-6">
                                    <div class="row">
                                        <div class="col-xl-5 col-sm-4">
                                            Fecha de llegada
                                        </div>
                                        <div class="col-xl-7 col-sm-8">
                                            : {{ trade.tra_arrival_date }}
                                        </div>
                                    </div>
                                </div>
                                <div class="col-md-6">
                                    <div class="row">
                                        <div class="col-xl-5 col-sm-4">
                                            Oficina de origen
                                        </div>
                                        <div class="col-xl-7 col-sm-8">
                                            : {{ trade.area.are_name }}
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="row py-2">
                                <div class="col-md-6">
                                    <div class="row">
                                        <div class="col-xl-5 col-sm-4">
                                            Estado
                                        </div>
                                        <div v-if="trade.tra_state_law === 'P'" class="col-xl-7 col-sm-8">
                                            : Pendiente
                                        </div>
                                        <div v-else class="col-xl-7 col-sm-8">
                                            : Finalizado
                                        </div>
                                    </div>
                                </div>
                                <div class="col-md-6">
                                    <div class="row">
                                        <div class="col-xl-5 col-sm-4">
                                            Estado
                                        </div>
                                        <div class="col-xl-7 col-sm-8">
                                            : DATA
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="row py-2">
                                <div class="col-md-6">
                                    <div class="row">
                                        <div class="col-xl-5 col-sm-4">
                                            Expediente de recepción
                                        </div>
                                        <div v-if="trade.tra_number_ext != null" class="col-xl-7 col-sm-8">
                                            : {{ trade.tra_number_ext }}
                                        </div>
                                        <div v-else class="col-xl-7 col-sm-8">
                                            : No se registro
                                        </div>
                                    </div>
                                </div>
                                <div class="col-md-6">
                                    <div class="row">
                                        <div class="col-xl-5 col-sm-4">
                                            Oficio de recepción
                                        </div>
                                        <div v-if="trade.tra_doc_recep != null" class="col-xl-7 col-sm-8">
                                            : {{ trade.tra_doc_recep }}
                                        </div>
                                        <div v-else class="col-xl-7 col-sm-8">
                                            : No se registró
                                        </div>
                                    </div>
                                </div>
                                
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="row">
            <div class="col-sm-12">
                <div class="card">
                    <div class="card-header">
                        <h5 class="card-title">Responsables</h5>
                    </div>
                    <div class="card-body">
                        <div class="table-responsive">
                            <table class="table table-hover" id="table" ref="myDataTable">
                                <thead>
                                    <tr>
                                        <th>DNI</th>
                                        <th>Nombres</th>
                                        <th>Correo</th>
                                        <th>Teléfono</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="person in persons" :key="person.per_id">
                                        <td>{{ person.nat_dni }}</td>
                                        <td>{{ person.nat_nombres}}</td>
                                        <td>{{ person.nat_correo || '---' }}</td>
                                        <td>{{ person.nat_telefono || '---' }}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
  
<script>
import feather from "feather-icons";
import DataTable from '@/utils/dataTable';
import moment from 'moment';

export default {
    mixins: [DataTable],
    data() {
        return {
            dataTable: null,
            trade: null,
            persons: [],
            formattedDate: "",
            id: '',
        };
    },
    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
        this.cargarDetallesOficio();
    },
    created() {
        this.sector = this.$store.getters.get__sector;
        this.id = this.$route.params.id;
    },
    methods: {
        async cargarDetallesOficio() {
            try {
                const response = await this.$getData(`trade/${this.id}`, this);
                this.trade = response.trade;
                await this.$waitForDataTableDraw(this, 'myDataTable');
                this.formatDate();
                this.initDataTable();
                this.$closeLoadingAlert();
            } catch (error) {
                this.$handleApiError(error, 'cargar detalles del expediente administrativo ', this.id);
            }
        },
        formatDate() {
            this.formattedDate = moment(this.trade.tra_arrival_date).format('DD MMM YYYY');
        },

        goBack() {
            this.$router.go(-1);
        },
    },
};
</script>
  